.carsListItem {
  width: 274px;
  position: relative;
}

.carsListItem-image {
  width: 100%;
  height: 268px;
  border-radius: 12px;
  object-fit: cover;
  margin-bottom: 14px;
  /* transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1); */
}
.addCar {
  position: absolute;
}
.itemContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 8px;
  flex-grow: 1;
}

.itemContainerTitlePrice {
  width: 265px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  color: black;
  margin: 0px;
}
.itemTitle {
  min-width: 30px;
  color: rgb(235, 228, 228);

  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  font-size: 14px;
}

.itemPrice {
  min-width: 30px;
  font-size: 18px;
  font-weight: 300;
  color: #ed9bb7;
}

.itemInfo {
  width: 265px;
  height: 50px;
  margin-bottom: 28px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
  padding: 0px;
  gap: 10px;
  color: rgb(126, 122, 122);
  font-size: 12px;
}
.itemInfoItem {
  font-size: 12px;
  color: rgb(194, 188, 188);
  border-right: 1px solid rgb(194, 188, 188);
  padding-right: 10px;
}
.itemInfoItemLast {
  font-size: 12px;
  color: rgb(194, 188, 188);
  border-right: none;
  padding-right: 10px;
}
